import React, { useState, useEffect } from "react";

import { BrowserPDF417Reader } from "@zxing/browser";

function App() {
  let timeOutId;

  const [selectedDeviceId, setSelectedDeviceId] = useState("");

  const [code, setCode] = useState("");

  const [videoInputDevices, setVideoInputDevices] = useState([]);

  const [scan, setScan] = useState(false);

  const codeReader = new BrowserPDF417Reader();

  console.log("ZXing code reader initialized");

  useEffect(() => {
    BrowserPDF417Reader.listVideoInputDevices()

      .then((videoInputDevices) => {
        setupDevices(videoInputDevices);
      })

      .catch((err) => {
        console.error(err);
      });

    return () => clearTimeout(timeOutId);
  }, []);

  function setupDevices(videoInputDevices) {
    //selectsfirstdevice

    setSelectedDeviceId(videoInputDevices[0].deviceId); //setupdevicesdropdown

    if (videoInputDevices.length >= 1) {
      setVideoInputDevices(videoInputDevices);
    }
  }

  function stop() {
    setScan(false);
  }

  function start() {
    setCode("");

    setScan(true);
  }

  async function decodeContinuously(selectedDeviceId) {
    const controls = await codeReader.decodeFromVideoDevice(
      selectedDeviceId,

      "video",

      (result, err, controls) => {
        if (result) {
          //properlydecodedqrcode

          console.log("Found QR code!", result);

          setCode(result.text);

          controls.stop();

          setScan(false);
        }

        if (err) {
          setCode("");

          console.log("Not found");
        }
      }
    );

    timeOutId = setTimeout(() => {
      controls.stop();

      setScan(false);
    }, 10000);
  }

  useEffect(() => {
    if (scan) {
      decodeContinuously(selectedDeviceId);

      console.log(`Started decode from camera with id ${selectedDeviceId}`);
    }
  }, [selectedDeviceId, scan]);

  return (
    <main className="wrapper">
      <section className="container" id="demo-content">
        <div id="sourceSelectPanel">
          <label htmlFor="sourceSelect">Change video source:PUBLISHED 2:58</label>
          <select
            id="sourceSelect"
            onChange={(sourceSelect) => setSelectedDeviceId(sourceSelect.value)}
          >
            {videoInputDevices.map((element, idx) => (
              <option key={idx} value={element.deviceId}>
                {element.label}
              </option>
            ))}
          </select>
        </div>
        <div>
          <video id="video" width="300" height="200" />
        </div>
        <label>Result:</label>
        <pre>
          <code id="result">{code}</code>
        </pre>
        <button
          onClick={() => {
            scan ? stop() : start();
          }}
        >
          {scan ? "stop" : "start"}
        </button>
      </section>
    </main>
  );
}

export default App;
